//vuex
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex)

const actions={
    openloginstore(context,value) {
        context.commit('Openloginstore',value)
    },
    shutloginstore(context,value) {
        context.commit('Shutloginstore',value)
    },
    shutappstore(context,value) {
        context.commit('Shutappstore',value)
    },
    backappstore(context,value) {
        context.commit('Backappstore',value)
    },
    
}//响应组件的动作
const mutations={
    Openloginstore(state,value) {
        state.showcontent =true
    },
    Shutloginstore(context,value) {
        state.showcontent =false
    },
    Shutappstore(state,value) {
        state.showroutercontent =false
    },
    Backappstore(state,value) {
        state.showroutercontent =true
    },
    
}//用于操作数据
const state={
    showcontent:false ,
    showroutercontent:true  
}//用于存储数据
const getters={
    
}//用于原始数据的更新加工

//导出并创建store
export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})


