import VueRouter from 'vue-router' 
import Vue from 'vue'

import ContentC from '../pages/ContentC.vue'  
import TestC from '../pages/TestC.vue'
import SearchC from '../pages/SearchC.vue'
Vue.use(VueRouter)

export default new VueRouter({
    routes:[
        {
            path:'/ContentC',
            component:ContentC
        },
        {
            path:'/TestC',
            component:TestC
        },
        {
            path:'/SearchC',
            component:SearchC
        }
    ],
})
const originalPush = VueRouter.prototype.push
    VueRouter.prototype.push = function push(location) {
        return originalPush.call(this, location).catch(err => err)
}
